#pengaduan {
  background-color: #eaedef;
  position: relative;
  font-size: 1.2rem;
}
#pengaduan > .wrapper {
  margin: auto;
  width: 50%;
}
#pengaduan > .wrapper > h3 {
  text-align: center;
  font-size: 2rem;
  margin: 0px 0px 30px 0px;
  padding: 0px;
  font-family: "Kanit", sans-serif;
  color: #333;
  letter-spacing: 5px;
  text-transform: uppercase;
}
#pengaduan > .wrapper > p {
  text-align: center;
}

#pengaduan > .wrapper > h4 {
  text-align: center;
  font-size: 1.4rem;
  margin: 0px 0px 20px 0px;
  padding: 0px;
  font-family: "Kanit", sans-serif;
  letter-spacing: 5px;
  color: #333;
}

.tombol-pengaduan {
  display: block; /* Pastikan tombol berperilaku sebagai blok untuk margin: auto */
  width: fit-content; /* Atau tentukan lebar spesifik, misalnya 150px */
  margin: 20px auto; /* Menengahkan secara horizontal dan menambahkan margin atas/bawah */
  padding: 10px 20px; /* Padding di dalam tombol */
  background-color: #4177e2; /* Warna biru */
  color: white; /* Warna teks putih agar kontras dengan latar belakang biru */
  border: none; /* Hilangkan border default tombol */
  border-radius: 5px; /* Sedikit sudut membulat */
  cursor: pointer; /* Menunjukkan bahwa elemen dapat diklik */
  font-size: 16px; /* Ukuran font */
  text-decoration: none; /* Hapus garis bawah dari link jika ada */
}

.tombol-pengaduan:hover {
  background-color: #0056b3; /* Sedikit lebih gelap saat di-hover */
}

/* Jika Anda ingin menata gaya link di dalamnya */
.tombol-pengaduan a {
  color: inherit; /* Mewarisi warna teks dari tombol induk */
  text-decoration: none; /* Pastikan link tidak memiliki garis bawah */
}

@media only screen and (max-width: 1000px) {
  #pengaduan > .wrapper {
    width: 80%;
  }
}