nav {
  background-color: #ffffff;
  color: #444444;
  position: sticky;
  top: 0;
  z-index: 100;
  text-transform: uppercase;
  box-shadow: 0px 0 18px rgba(0, 0, 0, 0.1);
}
nav > .wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu {
  width: 45%;
}

.logo {
  display: flex;
  align-items: center;
}

.logo > a {
  color: #444444;
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: 900;
  letter-spacing: 8px;
}

.logo img {
  max-height: 30px; /* Adjust this value as needed. This sets a maximum height. */
  width: auto; /* Ensures the aspect ratio is maintained. */
  vertical-align: middle; /* Helps align the image with text, if any */
}

.menu > ul {
  display: flex;
  list-style-type: none;
  margin: 0px;
  padding: 0px;
  justify-content: space-between;
  font-weight: 500;
}

.menu > ul > li > a {
  text-decoration: none;
  color: #444444; 
}

nav > .wrapper > button {
  cursor: pointer;
  display: none;
  position: absolute;
  right: 10px;
  top: 10px;
  border: 0px;
  background-color: transparent;
  padding: 0px;
}

nav > .wrapper > button > svg {
  color: #444444;
  font-size: 50px;
}


@media only screen and (max-width: 1000px) {
  .menu > ul {
    display: none;
  }
  .menu.tampil > ul {
    display: inline;
  }
  .menu {
    text-align: right;
  }
  nav > .wrapper > button {
    display: inline;
  }
  .menu > ul {
    flex-direction: column;
    background-color: #fff;
    width: 100%;
    position: fixed;
    top: 70px;
    left: 0;
  }
  .menu > ul > li {
    text-align: left;
    cursor: pointer;
  }
  .menu > ul > li > a {
    display: inline-block;
    padding: 15px 20px 15px 50px;
    width: 100%;
  }

  .menu > ul > li > a:hover {
    background-color: #cfcfcf;
  }
}