
.wrapper > h5 {
  text-align: center;
  font-size: 2rem;
  margin: 0px 0px 30px 0px;
  padding: 0px;
  font-family: "Kanit", sans-serif;
  color: #222831;
  letter-spacing: 5px;
  text-transform: uppercase;
  border-bottom: 5px solid #cccccc;
}
.wrapper > h5::after {
  content: "";
  display: block;
  border-bottom: 5px solid #4177e2;
  width: 45%;
  position: relative;
  bottom: -5px; /* your padding + border-width */
  margin: auto;
}

#tentang-kami > .wrapper > h4 {
  text-align: center;
  font-size: 1.4rem;
  margin: 0px 0px 4px 0px;
  padding: 0px;
  font-family: "Kanit", sans-serif;
  letter-spacing: 5px;
  color: #333;
}

#tentang-kami > .wrapper > p {
  text-align: center;
}

.wrapper {
  max-width: 900px; /* Sesuaikan lebar maksimal yang Anda inginkan */
  margin: 0 auto;   /* Menengahkan .wrapper di halaman */
  padding: 5px;    /* Padding di dalam wrapper */
  text-align: center; /* Opsional: jika ada teks lain di dalam wrapper yang ingin ditengahkan */
}

.brand-container {
  display: flex;                /* Mengaktifkan Flexbox untuk menyusun item secara horizontal */
  justify-content: center;      /* Menengahkan item-item (gambar) secara horizontal di dalam kontainer */
  align-items: center;          /* Menengahkan item-item secara vertikal (jika tingginya berbeda) */
  flex-wrap: wrap;              /* Mengizinkan item untuk pindah ke baris baru jika ruang tidak cukup */
  gap: 50px;                    /* Memberi jarak antar gambar (lebih modern dari margin pada item) */
  margin-top: 40px;             /* Opsional: jarak dari elemen di atasnya */
  margin-bottom: 0px;
           /* Opsional: jarak ke elemen di bawahnya */
}

.brand-container a {
  /* Opsional: untuk memastikan link tidak menambah ruang ekstra */
  display: block; /* Atau inline-block */
  line-height: 0; /* Menghilangkan spasi ekstra di bawah gambar jika link adalah inline */
}

.brand-container img {
  max-width: 150px; /* Atur lebar maksimum yang Anda inginkan untuk setiap gambar */
  height: auto;     /* Pertahankan rasio aspek gambar */
  /* Opsional: gaya tambahan */
  transition: transform 0.2s ease-in-out; /* Efek zoom saat hover */
}

.brand-container img:hover {
  transform: scale(1.05); /* Sedikit membesar saat di-hover */
}

.kontak {
  /* Jika .kontak ini adalah kontainer utama untuk seluruh bagian kontak (termasuk peta) */
  text-align: center; /* Untuk menengahkan konten inline/inline-block di dalamnya */
  padding-top: 1px; /* Opsional: jarak atas */
  padding-bottom: 30px; /* Opsional: jarak bawah */
}

.socialMedia {
  display: flex; /* Menggunakan Flexbox untuk menyusun ikon secara horizontal */
  justify-content: center; /* Menengahkan ikon secara horizontal */
  align-items: center; /* Menyelaraskan ikon secara vertikal (jika tingginya berbeda) */
  gap: 30px; /* Memberikan jarak antara setiap ikon */
  margin-bottom: 30px; /* Jarak antara ikon dan elemen di bawahnya (peta) */
  /* Opsional: Tambahkan padding jika ingin ruang di dalam socialMedia container */
  /* padding: 10px; */
}

.socialMedia a {
  /* Gaya untuk link pembungkus ikon */
  text-decoration: none; /* Menghilangkan garis bawah pada link */
  color: #ffffff; /* Warna biru untuk ikon (ini akan diwarisi oleh SVG) */
  font-size: 20px; /* Ukuran ikon, sesuaikan sesuai kebutuhan */
  transition: color 0.3s ease, transform 0.2s ease; /* Efek transisi saat hover */
  display: flex; /* Menggunakan flexbox untuk memastikan ikon berada di tengah lingkaran jika Anda menambahkannya */
  align-items: center;
  justify-content: center;
  /* Jika Anda ingin membuat lingkaran di sekitar ikon */
  width: 60px; /* Lebar lingkaran */
  height: 60px; /* Tinggi lingkaran */
  border: 2px solid #007bff; 
  border-radius: 20%; 
  background-color: #ffffff; 
}

.socialMedia a:hover {
  color: #ffffff; /* Warna biru yang sedikit lebih gelap saat hover */
  transform: translateY(-3px); /* Efek sedikit terangkat saat hover */
  border-color: #0056b3; /* Ubah warna border saat hover */
  background-color: #3262c2; /* Sedikit latar belakang biru transparan saat hover */
}

.socialMedia img {
  width: 32px; /* Mengatur lebar ikon menjadi 32 piksel */
  height: 32px; /* Mengatur tinggi ikon menjadi 32 piksel */
}

/* Jika Anda ingin menargetkan langsung SVG di dalam link (tidak selalu diperlukan karena warna diwarisi) */
.socialMedia a svg {
  /* color: #007bff; */ /* Bisa diatur di sini juga, tapi mewarisi dari parent lebih fleksibel */
}

.kontak-wrapper {
  display: flex;
  justify-content: center;      /* Center seluruh grup */
  align-items: flex-start;
  gap: 30px;                    /* Jarak antar elemen */
  flex-wrap: wrap;
  padding: 50px;
  background-color: #e0e0e0;    /* Sesuai warna background */
  max-width: 1000px;
  margin: 0 auto;
}

.alamat {
  display: flex;
  align-items: flex-start;
  flex-direction: row;
  max-width: 280px;
  text-align: left;             /* Rata kiri isi */
}

.ikon {
  font-size: 40px;
  color: #1a73e8;
  margin-right: 10px;
  margin-top: 2px;
}

.alamat h4 {
  margin: 0;
  font-size: 15px;
  font-weight: bold;
  text-align: left;             /* Pastikan judul rata kiri */
}

.alamat p {
  margin: 4px 0 0 0;
  font-size: 14px;
  color: #333;
  text-align: left;             /* Pastikan isi juga rata kiri */
}


@media only screen and (max-width: 1000px) {
  .grid {
    grid-template-columns: 45% 45%;
  }
}
