/* Dalam file CSS Anda */

.wrapper ol { /* Menargetkan SEMUA <ol> di dalam elemen dengan kelas 'wrapper' */
  list-style-type: lower-alpha;
  padding-left: 20px; /* Tambahkan indentasi agar penanda terlihat */
  margin-bottom: 5px;
}

/* Jika Anda ingin teks di dalam li sejajar dengan teks di paragraf lain */
.wrapper ol li {
  text-align: justify;
}

.wrapper {
  max-width: 800px; /* Atau sesuaikan dengan lebar konten maksimum yang Anda inginkan */
  margin: 0 auto;   /* Ini yang menengahkan .wrapper di halaman secara horizontal */
  padding: 20px;    /* Padding di dalam wrapper */
}

/* CSS untuk menengahkan gambar di dalam image-container */
.image-container {
  text-align: center; /* Ini menengahkan konten inline (termasuk gambar) di dalamnya */
  margin-top: 20px;   /* Opsional: beri jarak di atas gambar */
  margin-bottom: 20px; /* Opsional: beri jarak di bawah gambar */
}

/* Aturan untuk gambar itu sendiri agar responsif dan terlihat baik */
.image-container img {
  max-width: 100%;   /* Sangat penting: gambar tidak akan meluap dari container-nya */
  height: auto;      /* Menjaga rasio aspek gambar saat discale */
  display: block;    /* Mengubah gambar menjadi elemen blok */
  margin: 0 auto;    /* Menggunakan margin auto pada gambar yang display block untuk menengahkan */
  /* Opsional: gaya tambahan untuk visual */
  border-radius: 8px; /* Sedikit sudut membulat */
  box-shadow: 0 4px 8px rgba(0,0,0,0.1); /* Bayangan lembut */
}

p {
  text-align: left;
}
