#video {
  background-color: #eaedef;
  position: relative;
  font-size: 1.2rem;
  box-shadow: 0px 0 18px rgba(0, 0, 0, 0.1);
}
#video > .wrapper {
  margin: auto;
  width: 50%;
}

#video > .wrapper > h3 {
  text-align: center;
  font-size: 2.8rem;
  margin: 0px 0px 30px 0px;
  padding: 0px;
  font-family: "Kanit", sans-serif;
  color: #333;
  letter-spacing: 5px;
  text-transform: uppercase;
}

#video > .wrapper > p {
  text-align: center;
}

#video > .wrapper > h4 {
  text-align: center;
  font-size: 1.4rem;
  margin: 0px 0px 20px 0px;
  padding: 0px;
  font-family: "Kanit", sans-serif;
  letter-spacing: 5px;
  color: #333;
}

.video-container {
  text-align: center;
  margin-top: 20px; /* Opsional: beri jarak di atas gambar */
  margin-bottom: 20px;
}

.video-container iframe {
  max-width: 100%;   /* Sangat penting: gambar tidak akan meluap dari container-nya */
        /* Menjaga rasio aspek gambar saat discale */
  display: block;    /* Mengubah gambar menjadi elemen blok */
  margin: 0 auto;    /* Menggunakan margin auto pada gambar yang display block untuk menengahkan */
  /* Opsional: gaya tambahan untuk visual */
  border-radius: 8px; /* Sedikit sudut membulat */
  box-shadow: 0 4px 8px rgba(0,0,0,0.1); 
}

@media only screen and (max-width: 1000px) {
  #video > .wrapper {
    width: 80%;
  }
}
